.clear:after {
  display: block;
  clear: both;
  content: '';
}
.fl {
  float: left;
}
.fr {
  float: right;
}
.modal_setting {
  padding: 50px 20px;
}
.modal_setting .tailorWrap:after {
  display: block;
  clear: both;
  content: '';
}
.modal_setting .tailorWrap .tailor {
  float: left;
  width: 260px;
  height: 226px;
  display: table;
  background-color: #c3bebe;
}
.modal_setting .tailorWrap .tailor .tailorImgWrap {
  vertical-align: middle;
  display: table-cell;
}
.modal_setting .tailorWrap .tailor .tailorImgWrap > img {
  max-width: 226px;
  max-height: 226px;
  display: block;
  margin: auto;
}
.modal_setting .tailorWrap .tailorPreview {
  float: left;
  background-color: #c3bebe;
  width: 90px;
  height: 90px;
  border-radius: 50%;
  margin-left: 40px;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}
.modal_setting .modal_setting_btns {
  margin-top: 30px;
  text-align: center;
}
.modal_setting .modal_setting_btns:after {
  display: block;
  clear: both;
  content: '';
}
.modal_setting .modal_setting_btns > * {
  width: 90px;
  height: 30px;
  line-height: 30px;
  text-align: center;
  color: white;
  border-radius: 4px;
  font-size: 14px;
}
.modal_setting .modal_setting_btns .upload {
  float: left;
  background-color: #0092DC;
  position: relative;
}
.modal_setting .modal_setting_btns .upload .file {
  opacity: 0;
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  cursor: pointer;
  left: 0;
}
.modal_setting .modal_setting_btns .sure {
  float: right;
  cursor: pointer;
  background-color: #666;
}
