.PMTitle {
  width: 100%;
  position: relative;
}
.PMTitle > h6 {
  padding-left: 20px;
  font-size: 16px;
  color: #333;
  position: relative;
  height: 22px;
  line-height: 22px;
  letter-spacing: 1px;
}
.PMTitle > h6:before {
  background: #0092DC;
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  display: block;
  content: "";
  width: 5px;
}
.PMTitle.type_1 h6 {
  padding-left: 0;
  font-size: 18px;
  color: #333333;
  padding-bottom: 20px;
}
.PMTitle.type_1 h6:before {
  display: none;
}
