.middle > * {
  display: inline-block;
  vertical-align: middle;
  font-size: 0;
}
.clear:after {
  display: block;
  clear: both;
  content: '';
}
.memberCenterTelAutoPayWrap .tel {
  margin: 30px auto 0;
  text-align: center;
  font-size: 16px;
  color: #333333;
}
.memberCenterTelAutoPayWrap .btnWrap {
  margin: 20px auto 0;
  width: 180px;
}
.memberCenterTelAutoPayWrap .btnWrap > a {
  display: inline-block;
  vertical-align: middle;
  font-size: 12px;
  color: #333;
  cursor: pointer;
}
.memberCenterTelAutoPayWrap .surePay {
  margin-top: 50px;
  padding-bottom: 20px;
}
.memberCenterTelAutoPayWrap .surePay > div {
  width: 160px;
  height: 40px;
  border: none;
  color: #FFFFFF;
  border-radius: 4px;
  text-align: center;
  font-size: 16px;
  cursor: pointer;
  background-color: #0092DC;
  letter-spacing: 0.19px;
  line-height: 40px;
  margin: auto;
}
.memberCenterTelAutoPayWrap .surePay > div.forbidden {
  opacity: 0.5;
  cursor: not-allowed;
}
