.middle > * {
  display: inline-block;
  vertical-align: middle;
  font-size: 0;
}
.clear:after {
  display: block;
  clear: both;
  content: '';
}
.historyWrap {
  padding: 0 10px;
}
.historyWrap .sureModalContent {
  text-align: center;
  font-size: 20px;
  color: #333333;
}
.historyWrap .list .listModules:not(:first-child) {
  margin-top: 25px;
}
.historyWrap .list .listModules .listItem {
  vertical-align: top;
  display: inline-block;
  margin-bottom: 10px;
}
.historyWrap .list .listModules .listItem:not(:nth-child(5n + 1)) {
  margin-right: 25px;
}
