.oneLine {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  word-break: break-all;
}
.PCCard {
  width: 140px;
  position: relative;
  cursor: pointer;
}
.PCCard > i {
  display: block;
  height: 90px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  margin-bottom: 2px;
  position: relative;
}
.PCCard > i.noneImg {
  background-image: url(../assets/default_img_H_177.png);
}
.PCCard > i .info {
  font-size: 10px;
  position: absolute;
  bottom: 4px;
  right: 4px;
  padding: 3px;
  text-align: center;
  color: white;
  background-color: rgba(0, 0, 0, 0.6);
  font-style: normal;
}
.PCCard > i .playIcon {
  position: absolute;
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0);
  top: 0;
  left: 0;
  visibility: hidden;
  transition: all 0.3s;
}
.PCCard > i .playIcon:before {
  display: block;
  content: "";
  position: absolute;
  width: 27px;
  height: 27px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  background-image: url(data:image/png;base64,);
  left: 50%;
  top: 50%;
  margin-left: -13px;
  margin-top: -13px;
  transition: all 0.3s;
  -webkit-transform: scale(0, 0);
      -ms-transform: scale(0, 0);
          transform: scale(0, 0);
}
.PCCard .title {
  font-size: 14px;
  color: #333333;
  text-align: center;
  margin-bottom: 4px;
  height: 21px;
  line-height: 21px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  word-break: break-all;
}
.PCCard .type {
  text-align: left;
  font-size: 12px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  word-break: break-all;
  color: #999999;
}
.PCCard .del {
  display: none;
  position: absolute;
  width: 15px;
  height: 15px;
  cursor: pointer;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  background-image: url(data:image/png;base64,);
  right: -8px;
  top: -8px;
}
.PCCard:hover .del {
  display: block;
}
.PCCard:hover > i .playIcon {
  visibility: visible;
  background-color: rgba(0, 0, 0, 0.3);
}
.PCCard:hover > i .playIcon:before {
  -webkit-transform: scale(1, 1);
      -ms-transform: scale(1, 1);
          transform: scale(1, 1);
}
