.middle > * {
  display: inline-block;
  vertical-align: middle;
  font-size: 0;
}
.clear:after {
  display: block;
  clear: both;
  content: '';
}
.fl {
  float: left;
}
.fr {
  float: right;
}
.memberCenterModuleWrap {
  padding: 0 5px;
}
.memberCenterModuleWrap .openSuccess {
  padding: 20px;
}
.memberCenterModuleWrap .openSuccess .cardImg {
  width: 200px;
  height: 150px;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  background-image: url(../assets/memberGift.png);
  margin: auto;
}
.memberCenterModuleWrap .openSuccess .cardInfo {
  margin-top: 30px;
  font-size: 16px;
  color: #999999;
}
.memberCenterModuleWrap .openSuccess .cardInfo > li:not(:first-child) {
  margin-top: 8px;
}
.memberCenterModuleWrap .openSuccess .cardInfo > li > * {
  display: inline-block;
  vertical-align: middle;
}
.memberCenterModuleWrap .openSuccess .cardInfo > li > span {
  width: 220px;
  text-align: right;
}
.memberCenterModuleWrap .openSuccess .cardInfo > li > p {
  width: 300px;
  margin-left: 10px;
}
.memberCenterModuleWrap .item {
  padding: 20px 0;
}
.memberCenterModuleWrap .item:not(:last-child) {
  border-bottom: 1px solid #EEEEEE;
}
.memberCenterModuleWrap .item.card {
  padding-top: 0;
}
.memberCenterModuleWrap .pay .notAuto:after {
  display: block;
  clear: both;
  content: '';
}
.memberCenterModuleWrap .pay .notAuto .left,
.memberCenterModuleWrap .pay .notAuto .right {
  float: left;
  width: 100%;
}
