.middle > * {
  display: inline-block;
  vertical-align: middle;
  font-size: 0;
}
.clear:after {
  display: block;
  clear: both;
  content: '';
}
.memberCenterExchangeRightContent {
  padding: 60px 15px 40px;
}
.memberCenterExchangeRightContent .info {
  margin-top: 40px;
}
.memberCenterExchangeRightContent .info > li {
  font-size: 14px;
  color: #4A4A4A;
}
.memberCenterExchangeRightContent .info > li:not(:first-child) {
  margin-top: 5px;
}
.memberCenterExchangeRightContent .exchange > * {
  display: inline-block;
  vertical-align: middle;
  font-size: 0;
}
.memberCenterExchangeRightContent .exchange input {
  display: inline-block;
  width: 260px;
  height: 40px;
  line-height: 20px;
  box-sizing: border-box;
  padding: 10px;
  font-size: 16px;
  color: #999;
  background: #FAFAFA;
  border: 1px solid #E0E0E0;
  border-radius: 4px;
}
.memberCenterExchangeRightContent .exchange .btn {
  width: 80px;
  height: 40px;
  line-height: 40px;
  text-align: center;
  background: #0092DC;
  border-radius: 4px;
  font-size: 16px;
  cursor: pointer;
  color: white;
  margin-left: -8px;
}
