.middle > * {
  display: inline-block;
  vertical-align: middle;
  font-size: 0;
}
.clear:after {
  display: block;
  clear: both;
  content: '';
}
.collectWrap {
  padding: 0 10px;
}
.collectWrap .sureModalContent {
  text-align: center;
  font-size: 20px;
  color: #333333;
}
.collectWrap .list {
  margin-top: 15px;
}
.collectWrap .list:after {
  display: block;
  clear: both;
  content: '';
}
.collectWrap .list > li {
  float: left;
  margin-bottom: 10px;
}
.collectWrap .list > li:not(:nth-child(5n)) {
  margin-right: 25px;
}
