.middle > * {
  display: inline-block;
  vertical-align: middle;
  font-size: 0;
}
.clear:after {
  display: block;
  clear: both;
  content: '';
}
.memberCenterCardWrap {
  min-height: 120px;
  box-sizing: content-box;
  padding-top: 30px;
}
.memberCenterCardWrap > * {
  display: inline-block;
  vertical-align: middle;
  font-size: 0;
}
.memberCenterCardWrap > li {
  height: 90px;
  width: 140px;
  position: relative;
}
.memberCenterCardWrap > li:not(:first-child) {
  margin-left: 27px;
}
