.alignMiddle {
  position: relative;
}
.alignMiddle:before {
  height: 100%;
  width: 0;
  content: "";
}
.alignMiddle > * {
  display: inline-block;
  vertical-align: middle;
  font-size: 0;
}
.fontSize14 {
  font-size: 14px;
}
.fontSize16 {
  font-size: 16px;
}
.color9 {
  color: #999;
}
.color6 {
  color: #666;
}
.color3 {
  color: #333;
}
.changePassword {
  padding: 30px 20px;
}
.changePassword:after {
  display: block;
  content: "";
  clear: both;
}
.changePassword ul {
  float: left;
  width: 500px;
}
.changePassword ul input,
.changePassword ul select,
.changePassword ul textarea {
  background: #FAFAFA;
  border: 1px solid #E0E0E0;
  padding: 10px;
  border-radius: 4px;
  box-sizing: border-box;
  height: 40px;
  font-size: 14px!important;
  outline: none;
}
.changePassword ul > li {
  position: relative;
}
.changePassword ul > li:before {
  height: 100%;
  width: 0;
  content: "";
}
.changePassword ul > li > * {
  display: inline-block;
  vertical-align: middle;
  font-size: 0;
}
.changePassword ul > li:not(:first-child) {
  margin-top: 30px;
}
.changePassword ul > li .title {
  width: 80px;
  font-size: 16px;
  color: #666;
  text-align: right;
  padding-right: 20px;
}
.changePassword ul > li.name input {
  box-sizing: border-box;
  height: 40px;
  width: 340px;
  color: #999;
}
.changePassword ul > li.tel .content > span {
  font-size: 14px;
  color: #999;
}
.changePassword ul > li.infro .title {
  vertical-align: top;
}
.changePassword ul > li.infro textarea {
  height: 120px;
  width: 340px;
  box-sizing: border-box;
}
.changePassword ul > li.submit .button {
  background: #0092DC;
  border-radius: 4px;
  color: white;
  line-height: 40px;
  font-size: 16px;
  height: 40px;
  width: 160px;
  text-align: center;
  cursor: pointer;
}
.changePassword ul > li.verCode .content {
  position: relative;
}
.changePassword ul > li.verCode .content:before {
  height: 100%;
  width: 0;
  content: "";
}
.changePassword ul > li.verCode .content > * {
  display: inline-block;
  vertical-align: middle;
  font-size: 0;
}
.changePassword ul > li.verCode .content .getCode {
  width: 120px;
  height: 40px;
  line-height: 40px;
  text-align: center;
  color: white;
  background: #0092DC;
  border-radius: 4px;
  font-size: 16px;
  margin-left: 27px;
  cursor: pointer;
}
