.clear:after {
  display: block;
  clear: both;
  content: '';
}
.middle > * {
  display: inline-block;
  vertical-align: middle;
  font-size: 0;
}
.PRubbishAction:after {
  display: block;
  clear: both;
  content: '';
}
.PRubbishAction > .rubbishWrap {
  cursor: pointer;
  float: right;
  width: auto;
}
.PRubbishAction > .rubbishWrap > * {
  display: inline-block;
  vertical-align: middle;
  font-size: 0;
}
.PRubbishAction > .rubbishWrap > i {
  height: 24px;
  width: 18px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-image: url(data:image/png;base64,);
}
.PRubbishAction > .rubbishWrap > span {
  font-size: 18px;
  color: #999999;
  margin-left: 5px;
}
