.PMsgItem {
  display: table;
  width: 100%;
}
.PMsgItem > * {
  display: table-cell;
  vertical-align: middle;
}
.PMsgItem > .icon {
  width: 78px;
  padding: 10px;
}
.PMsgItem > .icon > i {
  display: block;
  width: 58px;
  height: 58px;
  border-radius: 50%;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}
.PMsgItem > .icon > i.system {
  background-image: url(data:image/png;base64,);
}
.PMsgItem > .icon > i.zj {
  background-image: url(data:image/png;base64,);
}
.PMsgItem > .itemInfo .title {
  font-size: 14px;
  color: #999999;
  margin-bottom: 10px;
}
.PMsgItem > .itemInfo .subTitle {
  margin-bottom: 10px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  word-break: break-all;
}
.PMsgItem > .itemInfo .subTitle > span {
  font-size: 12px;
  color: #333333;
}
.PMsgItem > .itemInfo .subTitle > span:last-child {
  cursor: pointer;
  color: #B5A075;
  margin-left: 8px;
}
.PMsgItem > .itemInfo .time {
  font-size: 12px;
  color: #999999;
}
.PMsgItem > .action {
  vertical-align: bottom;
  cursor: pointer;
  font-size: 12px;
  color: #999999;
  width: 80px;
}
