.clear:after {
  display: block;
  clear: both;
  content: '';
}
.memberInterest ul {
  padding: 40px;
  text-align: center;
}
.memberInterest ul:after {
  display: block;
  clear: both;
  content: '';
}
.memberInterest ul > li {
  width: 80px;
  margin: 0 20px;
  display: inline-block;
}
.memberInterest ul > li:not(:first-child) {
  margin-left: 50px;
}
.memberInterest ul > li > i {
  display: block;
  margin: auto;
  width: 60px;
  height: 60px;
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
}
.memberInterest ul > li > i.interest_1 {
  background-image: url(data:image/png;base64,);
}
.memberInterest ul > li > i.interest_2 {
  background-image: url(data:image/png;base64,);
}
.memberInterest ul > li > i.interest_3 {
  background-image: url(data:image/png;base64,);
}
.memberInterest ul > li > i.interest_4 {
  background-image: url(data:image/png;base64,);
}
.memberInterest ul > li > p {
  margin-top: 10px;
  text-align: center;
  font-size: 16px;
  color: #999;
}
