.alignMiddle {
  position: relative;
}
.alignMiddle:before {
  height: 100%;
  width: 0;
  content: "";
}
.alignMiddle > * {
  display: inline-block;
  vertical-align: middle;
  font-size: 0;
}
.fontSize14 {
  font-size: 14px;
}
.fontSize16 {
  font-size: 16px;
}
.color9 {
  color: #999;
}
.color6 {
  color: #666;
}
.color3 {
  color: #333;
}
.clear:after {
  display: block;
  clear: both;
  content: '';
}
.fl {
  float: left;
}
.fr {
  float: right;
}
.personalInfoWrap {
  padding: 30px 20px;
}
.personalInfoWrap:after {
  display: block;
  content: "";
  clear: both;
}
.personalInfoWrap .left {
  float: left;
  width: 500px;
}
.personalInfoWrap .left input,
.personalInfoWrap .left select,
.personalInfoWrap .left textarea,
.personalInfoWrap .left .commonWrap {
  background: #FAFAFA;
  border: 1px solid #E0E0E0;
  padding: 10px;
  border-radius: 4px;
  font-size: 14px!important;
  outline: none;
}
.personalInfoWrap .left > li {
  position: relative;
}
.personalInfoWrap .left > li:before {
  height: 100%;
  width: 0;
  content: "";
}
.personalInfoWrap .left > li > * {
  display: inline-block;
  vertical-align: middle;
  font-size: 0;
}
.personalInfoWrap .left > li:not(:first-child) {
  margin-top: 30px;
}
.personalInfoWrap .left > li .title {
  width: 80px;
  font-size: 16px;
  color: #666;
  text-align: right;
  padding-right: 20px;
}
.personalInfoWrap .left > li.name {
  position: relative;
}
.personalInfoWrap .left > li.name input {
  box-sizing: border-box;
  height: 40px;
  width: 340px;
  color: #999;
}
.personalInfoWrap .left > li.name .nameStatus {
  position: absolute;
  font-size: 12px;
  left: 110px;
  color: red;
  bottom: -20px;
}
.personalInfoWrap .left > li.birthday select {
  width: 100px;
  box-sizing: border-box;
}
.personalInfoWrap .left > li.birthday select:not(:first-child) {
  margin-left: 20px;
}
.personalInfoWrap .left > li.tel .content > span {
  font-size: 14px;
  color: #999;
}
.personalInfoWrap .left > li.infro .title {
  vertical-align: top;
}
.personalInfoWrap .left > li.infro .content {
  position: relative;
  height: 120px;
  width: 340px;
  padding-bottom: 20px;
  box-sizing: border-box;
}
.personalInfoWrap .left > li.infro .content textarea {
  position: relative;
  padding: 0;
  border: none;
  border-radius: 0px;
  background-color: transparent;
  height: 100%;
  width: 100%;
}
.personalInfoWrap .left > li.infro .content > .tip {
  position: absolute;
  right: 5px;
  bottom: 3px;
  font-size: 12px;
  color: #999;
}
.personalInfoWrap .left > li.submit .button {
  background: #0092DC;
  border-radius: 4px;
  color: white;
  line-height: 40px;
  font-size: 16px;
  height: 40px;
  width: 160px;
  text-align: center;
  cursor: pointer;
}
.personalInfoWrap .left > li.submit .editPass {
  display: inline-block;
  vertical-align: bottom;
  font-size: 13px;
  margin-left: 18px;
  color: #0092DC;
  cursor: pointer;
}
.personalInfoWrap .right {
  width: 250px;
  float: right;
}
.personalInfoWrap .right .headImg {
  width: 120px;
  height: 130px;
  margin: auto;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  background-color: #FAFAFA;
  cursor: pointer;
}
.personalInfoWrap .right .headImg.defaultImg {
  background-image: url(data:image/png;base64,);
}
.personalInfoWrap .right > p {
  text-align: center;
  color: #333;
  font-size: 14px;
  margin-top: 10px;
}
