.middle > * {
  display: inline-block;
  vertical-align: middle;
  font-size: 0;
}
.clear:after {
  display: block;
  clear: both;
  content: '';
}
.memberCenterQRCodePayLeftContent {
  padding: 50px 0;
}
.memberCenterQRCodePayLeftContent .code {
  display: block;
  margin: auto;
  width: 300px;
}
.memberCenterQRCodePayLeftContent .code:after {
  display: block;
  clear: both;
  content: '';
}
.memberCenterQRCodePayLeftContent .code > div {
  /*float: left;*/
  width: 125px;
  margin: auto;
}
.memberCenterQRCodePayLeftContent .code > div:first-child {
  /*margin-right: 50px;*/
}
.memberCenterQRCodePayLeftContent .code > div > .codeImg {
  display: block;
  width: 100%;
  height: 125px;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  position: relative;
}
.memberCenterQRCodePayLeftContent .code > div > .codeImg.noneImg {
  background-image: url(../assets/loading.gif);
  background-size: 60px;
}
.memberCenterQRCodePayLeftContent .code > div > p {
  font-size: 12px;
  text-align: center;
  color: #333;
}
.memberCenterQRCodePayLeftContent .btnWrap {
  margin: 50px auto 0;
  width: 180px;
}
.memberCenterQRCodePayLeftContent .btnWrap > a {
  display: inline-block;
  vertical-align: middle;
  font-size: 12px;
  color: #333;
  cursor: pointer;
}
