.PMCardWrap {
  cursor: pointer;
  height: 100%;
  width: 100%;
  position: relative;
  display: table;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}
.PMCardWrap .select {
  bottom: 0;
  z-index: 2;
  right: 0;
  position: absolute;
  width: 24px;
  height: 24px;
  background-image: url(data:image/png;base64,);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}
.PMCardWrap .content {
  display: table-cell;
  vertical-align: middle;
  color: white;
  text-align: center;
}
.PMCardWrap .content > h6 {
  font-size: 20px;
  color: white;
}
.PMCardWrap .content > p {
  margin-top: 5px;
  font-size: 12px;
  color: white;
}
.PMCardWrap.type_1 {
  background-image: url(../assets/year.png);
}
.PMCardWrap.type_2 {
  background-image: url(../assets/halfYear.png);
}
.PMCardWrap.type_3 {
  background-image: url(../assets/quarter.png);
}
.PMCardWrap.type_4 {
  background-image: url(../assets/monthly.png);
}
.PMCardWrap.type_5 {
  background-image: url(../assets/autoRenew.png);
}
