.PSideContent {
  position: relative;
}
.PSideContent .userInfo {
  padding: 20px 0 10px;
  margin: 0 15px;
  border-bottom: 1px solid #EEEEEE;
}
.PSideContent .userInfo .headImg {
  display: block;
  margin: auto;
  width: 60px;
  height: 60px;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  border-radius: 50%;
  background-image: url(data:image/png;base64,);
}
.PSideContent .userInfo .userName {
  margin-top: 10px;
  text-align: center;
  overflow: hidden;
  text-overflow: ellipsis;
  word-break: break-all;
  white-space: nowrap;
  font-size: 14px;
  color: #333;
}
.PSideContent .userInfo .userName > span {
  max-width: 160px;
  display: inline-block;
  vertical-align: middle;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  word-break: break-all;
}
.PSideContent .userInfo .userName .vipIcon {
  width: 12px;
  height: 12px;
  vertical-align: middle;
  display: inline-block;
  margin-left: 2px;
  margin-top: 3px;
  background-repeat: no-repeat;
  background-position-x: 1px;
  background-position-y: 0px;
  background-size: 100px;
  background-image: url(../assets/icon_sprite.png);
}
.PSideContent .userInfo .userName .vipIcon.active {
  background-position-y: -15px;
}
.PSideContent .memberOrder {
  font-size: 0;
  padding: 10px 15px;
}
.PSideContent .memberOrder .memberInfo {
  font-size: 12px;
  text-align: center;
  color: #999;
  line-height: 24px;
}
.PSideContent .memberOrder .openInfo {
  display: inline-block;
  font-size: 12px;
  color: #999;
  vertical-align: middle;
}
.PSideContent .memberOrder .open {
  cursor: pointer;
  width: 60px;
  line-height: 24px;
  height: 24px;
  text-align: center;
  background: #0092DC;
  border-radius: 2px;
  color: white;
  font-size: 12px;
  vertical-align: middle;
  margin-left: 10px;
  display: inline-block;
}
.PSideContent .menu li {
  width: 100%;
  cursor: pointer;
  box-sizing: border-box;
  margin: 10px 0;
  height: 50px;
  display: table;
  line-height: 30px;
  padding: 10px 0 10px 60px;
}
.PSideContent .menu li .iWrap {
  display: table-cell;
  vertical-align: middle;
  width: 20px;
}
.PSideContent .menu li .iWrap > i {
  width: 20px;
  display: block;
  height: 20px;
  background-repeat: no-repeat;
  background-position-x: 1px;
  background-size: 90px;
  background-image: url(../assets/icon_sprite.png);
}
.PSideContent .menu li .iWrap > i.settingSide {
  background-position-y: -25px;
}
.PSideContent .menu li .iWrap > i.memberSide {
  background-position-y: -80px;
}
.PSideContent .menu li .iWrap > i.collectSide {
  background-position-y: -133px;
}
.PSideContent .menu li .iWrap > i.filmSide {
  background-position-y: -186px;
}
.PSideContent .menu li .iWrap > i.historySide {
  background-position-y: -239px;
}
.PSideContent .menu li .iWrap > i.msgSide {
  background-position-y: -292px;
}
.PSideContent .menu li > span {
  padding-left: 12px;
  display: table-cell;
  vertical-align: middle;
  font-size: 16px;
  color: #999999;
  letter-spacing: 0.69px;
}
.PSideContent .menu li.menu_active,
.PSideContent .menu li:hover {
  background: #EEEEEE;
}
.PSideContent .menu li.menu_active .iWrap > i.settingSide,
.PSideContent .menu li:hover .iWrap > i.settingSide {
  background-position-y: -52px;
}
.PSideContent .menu li.menu_active .iWrap > i.memberSide,
.PSideContent .menu li:hover .iWrap > i.memberSide {
  background-position-y: -107px;
}
.PSideContent .menu li.menu_active .iWrap > i.collectSide,
.PSideContent .menu li:hover .iWrap > i.collectSide {
  background-position-y: -160px;
}
.PSideContent .menu li.menu_active .iWrap > i.filmSide,
.PSideContent .menu li:hover .iWrap > i.filmSide {
  background-position-y: -213px;
}
.PSideContent .menu li.menu_active .iWrap > i.historySide,
.PSideContent .menu li:hover .iWrap > i.historySide {
  background-position-y: -266px;
}
.PSideContent .menu li.menu_active .iWrap > i.msgSide,
.PSideContent .menu li:hover .iWrap > i.msgSide {
  background-position-y: -319px;
}
.PSideContent .menu li.menu_active > span,
.PSideContent .menu li:hover > span {
  color: #0092DC;
}
