.clear:after {
  display: block;
  content: "";
  clear: both;
}
.personalWrap {
  padding-top: 90px ;
  padding-bottom: 80px;
}
.personalWrap .personal {
  width: 1080px;
  margin: auto;
  position: relative;
  background-color: white;
  min-height: 780px;
  display: table;
}
.personalWrap .personal:after {
  display: block;
  content: "";
  clear: both;
}
.personalWrap .personal > .side {
  position: relative;
  display: table-cell;
  vertical-align: top;
  width: 220px;
  height: 780px;
  z-index: 2;
  background-color: #f9f9f9;
}
.personalWrap .personal > .personalContent {
  display: table-cell;
  vertical-align: top;
  position: relative;
  z-index: 3;
}
.personalWrap .personal > .personalContent > .personalContentWrap {
  padding: 20px;
  box-sizing: border-box;
}
